import React, {useState} from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import './index.css'
import { ko } from "date-fns/esm/locale";

const SubModal = (props) => {
    
    const {open, close, create} = props;
    //open open , close close  . project  input .

    const [startDate, setStartDate] = useState();
    const [endDate, setendDate] = useState();

    return(
        <div className={ open ? 'openModal modal' : 'modal' }>
            { open ? (  
                <section>
                    <header>
                        <p>
                            Create new Project! <button onClick={close}> close </button>
                        </p>  
                    </header>
                    <main>
                        <p>Title: <input id='taskname'/></p>
                        <p>Explanation: <input id='exp'/></p>
                        <p>Start:
                        <DatePicker id='startdate'
                            locale={ko}	//   
                            dateFormat="yyyy-MM-dd"	//   
                            minDate={new Date()}	//       
                            closeOnScroll={true}	//        false
                            selected={startDate}
                            onChange={Date => setStartDate(Date)}
                        />
                        </p>

                        <p>End:
                        <DatePicker id='enddate'
                            locale={ko}	//   
                            dateFormat="yyyy-MM-dd"	//   
                            minDate={new Date()}	//       
                            closeOnScroll={true}	//        false
                            selected={endDate}
                            onChange={Date => setendDate(Date)}
                        />
                        </p>

                        <p>Todo: <input id='todo'/></p>
                        <p>Related Schedule: <input id='related'/></p>
                    </main>
                    <footer>
                        <button onClick={create}> Create </button>
                    </footer>
                </section>
            ) : null }
        </div>
    )
}

export default SubModal;